@import url(https://fonts.googleapis.com/css?family=Tangerine);
body {
  height: 100%;
  min-height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

button{
  cursor: pointer;
}
button:focus {outline:0;}

.home {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home__hello{
  font-size: 2em;
  font-family: helvetica, serif;
}

.PopupCreateNews{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  top: 50%;
  margin-top: -300px;
  left: 50%;
  margin-left: -250px;
  min-width: 600px;
  min-height: 500px;
  background: linear-gradient(#f0eaf5, #f6e9f8);
  border: 1px solid #a1a1bd;
  box-shadow: 0 30px 40px rgba(0,0,0,.2);
}
.PopupCreateNews h3{
  margin: 0 0 20px 0;
  color: #252526;
}
.PopupCreateNews__close{
  position: absolute;
  top: 5px;
  margin-left: 90%;
  margin-top: 10px;
  height: 30px;
  width: 30px;
  background-color: #abf5f3;
  border-color: #85c6c4;
  border-radius: 5px;
  font-weight: bold;
  font-size: 0.8em;
  text-align: center;
  cursor: pointer;
}
.PopupCreateNews--transitionIn{
  animation-name: modalFadeIn;
  animation-duration: .3s;
}
@keyframes modalFadeIn {
  from {opacity: 0;}
  to {opacity: 1;}
}
.PopupCreateNews--transitionOut{
  animation-name: modalFadeOut;
  animation-duration: .3s;
}
@keyframes modalFadeOut {
  from {opacity: 1;}
  to {opacity: 0;}
}

.PopupCreateNews__content{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.PopupCreateNews__form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.PopupCreateNews__input{
  margin-bottom: 15px;
  /*max-width: 300px;*/
  height: 20px;
  font-family: Ubuntu, serif;
  text-align: center;
  font-size: 0.8em;
}
.input--title{
  font-family: 'Tangerine', serif;
  font-size: 1.5em;
  text-shadow: 4px 4px 4px #aaa;
  height: 60px;
  width: 80%;
  margin-bottom: 20px;
  border: 1px solid #ced4da;
}
.input--content{
  font-family: "DejaVu Sans", serif;
  display: block;
  width: 77%;
  height: 200px;
  text-align: left;
  padding: 15px;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .25rem;
}
.input--submit{
  height: 40px;
  background-color: #abf5f3;
  border-color: #85c6c4;
  border-radius: 5px;
  cursor: pointer;
}

.PopupCreateNews__error{
  margin-bottom: 10px;
  font-size: 0.8em;
  color: red;
}

.news{
  display: flex;
  flex-direction: column;
  justify-content: center;

  border: 3px solid rgba(161, 161, 189, .3);
  border-radius: 5px;
  margin-bottom: 40px;
  background-color: rgba(233, 228, 253, .2);
  font-family: "DejaVu Sans", serif;
  font-size: 0.9em;
}
.news--unapproved{
  background-color: rgba(133, 198, 196, .3);
  border-color: rgba(128, 128, 128, .3);
}
.news--light{
  background-color: white;
}
.news--dark{
  background-color: #363638;
}

.news__container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
}
.news__content{
  padding: 50px;
  padding-top: 20px;
  width: 90%;
}

.news__item{
  padding: 10px 15px 10px 15px;
  border-bottom: 1px solid rgba(161, 161, 189, .3);
}
.news__title{
  text-align: left;
  padding-left: 30px;
  font-family: 'Playfair', serif;
  font-size: 1.5em;
  text-shadow: 4px 4px 4px #aaa;
}
.news__text{

}
.news__date{
  text-align: right;
  opacity: 40%;
  font-size: 0.6em;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.news__header{
  width: 95%;
  display: flex;
  justify-content: space-between;
}
.create-news{
  flex: 1 1;
}
.news__search{
  flex: 3 1;
  margin-right: 15px;
  height: 30px;
  text-align: center;
  font-weight: bold;
  font-size: 0.8em;
  background-color: #efe7f5;
  border-radius: 5px;
  border: 1px solid #a1a1bd;
}
.news__search--dark{
  background-color: #727286;
}
.news__search::-webkit-input-placeholder{
  color: #b7b7d5;
}
.news__search:-ms-input-placeholder{
  color: #b7b7d5;
}
.news__search::-ms-input-placeholder{
  color: #b7b7d5;
}
.news__search::placeholder{
  color: #b7b7d5;
}
.news__search--dark::-webkit-input-placeholder{
  color: #d3d3ec;
}
.news__search--dark:-ms-input-placeholder{
  color: #d3d3ec;
}
.news__search--dark::-ms-input-placeholder{
  color: #d3d3ec;
}
.news__search--dark::placeholder{
  color: #d3d3ec;
}

.news__admin{
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.news__button{
  margin-right: 20px;
  height: 25px;
  border-radius: 5px;
  background-color: #e9e4fd;
}
.news__button--approve{

}
.news__button--delete{

}

.create-news{
  width: 95%;
  text-align: center;
  font-size: 0.8em;
  background-color: #efe7f5;
  border-radius: 5px;
  border: 1px solid #a1a1bd;
}

.layout{
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 24px;
  font-family: "Helvetica Neue", serif;
}

.layout__main{
  min-height: 100vh;
  margin-top: 50px;
}

.layout--dark{
  background-color: #252526;
  color: #e9e9e9;
}

.layout--light{
  background-color: #e9e9e9;
  color: #252526;
}

.layout.main{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}


.navbar{
  overflow: hidden;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}
.navbar--light{
  background: linear-gradient(#EFE7F5, #F6E9F8);
  border-bottom: 1px solid #a1a1bd;
  color: #252526;
}

.active{
  background-color: #d1d4ff;
  display: flex;
  align-items: center;
  height: 50px;
  width: 100%;
  padding: 0 10px 0 10px;
  margin-right: -5px;
}

.navbar__brand{
  padding-left: 20px;
  color: #55aee3;
  font-weight: bold;
  font-family: 'Tangerine', serif;
  font-size: 1.5em;
  text-shadow: 1px 1px 1px #aaa;
}
.navbar__list{
  display: flex;
  list-style: none;
  align-items: center;
}

.navbar__nav-item{
  margin-right: 20px;
  display: flex;
  align-items: center;
}
.nav-item__link{
  text-decoration: none;
  color: #29676d;
}
.nav-item__link:hover {
  text-decoration: underline;
}
.nav-item__link:active{
  color: black;
}
.nav-item__link:active {
  box-shadow: none;
  top: 5px;
}

.navbar__user-form{
  margin-left: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px solid rgba(133, 198, 196, .5);
  border-radius: 5px;
  padding: 5px;
}
.nav-item__button{
  height: 25px;
  margin-left: 10px;
  background-color: #e9e4fd;
  border-radius: 5px;
  border-color: #c8c4db;
}

.nav-item--palette{
  margin-right: 20px;
  display: flex;
  margin-left: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  align-items: center;
  border: 2px solid #a1a1bd;
  font-weight: bold;
  font-size: 0.9em;
  padding: 8px 0px 0 3px;
}

.palette--light{
  background-color: #EFE7F5;
  transition: all 0.3s;
}
.palette--dark{
  background-color: #252526;
  color: #EFE7F5;
  transition: all 0.3s;
}

